import { useEffect, useState } from "react";

const User = () => {

    const [userInfo, setUserInfo] = useState(null);
    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        const data = await fetch("https://api.github.com/users/nehamehar");
        const json = await data.json();
        setUserInfo(json);
    };
    if (!userInfo) {
    return <h2>Loading profile...</h2>;
  }
    const { name, location, avatar_url } = userInfo;
    return (
       <div className="p-2 text-center border border-black">
  <img
    src={avatar_url}
    alt="User Avatar"
    className="w-[150px] rounded-[10%] mx-auto mb-2"
  />
  <p>{name}</p>
  <p>Location: {location}</p>
  <p>Contact: nehamehar31@gmail.com</p>
</div>
    );
};

export default User;